import { Space, Typography, Divider, Tag } from "antd";
import React, { useState } from "react";

const App = () => {
  const [menu] = useState([
    {
      name: "$45 ~ $65",
    },
    {
      name: "$70 ~ $95",
    },
    {
      name: "$100 ~ $120",
    },
    {
      name: "$125 ~ $150",
    },
    {
      name: "$155 ~ $180",
    },
  ]);

  const [selectedTags, setSelectedTags] = useState(["$45 ~ $65"]);

  return (
    <>
      <Space direction="vertical" style={{ width: "100%" }}>
        <Divider orientation="left" orientationMargin="0">
          <Typography.Text>價格範圍</Typography.Text>
        </Divider>

        {menu.map((x, i) => {
          return (
            <Tag.CheckableTag
              key={i}
              checked={selectedTags.indexOf(x.name) > -1}
              onChange={(checked) => setSelectedTags(x.name)}
            >
              {x.name}
            </Tag.CheckableTag>
          );
        })}
      </Space>
    </>
  );
};

export default App;
