import Header from "@Components/Header/Header";
import {
  Button,
  List,
  Skeleton,
  Image,
  Row,
  Col,
  Divider,
  Typography,
  Space,
  Empty,
} from "antd";
import { useCart } from "@Utils/Cart/Cart";
import MockData from "@Mocks/MockData";
import { ProDescriptions } from "@ant-design/pro-components";
import { useNavigate } from "react-router-dom";

const App = () => {
  const cart = useCart();
  const navigate = useNavigate();
  const totalProduct = cart.current
    .map((x) => x.quality)
    .reduce((x, y) => x + y, 0);

  const totalAmount = cart.current
    .map((x) => {
      const data = MockData.all.find((x) => x.id === x.id);
      return data.price * x.quality;
    })
    .reduce((x, y) => x + y, 0);

  return (
    <>
      <div style={{ position: "fixed", top: 0, width: "100%", zIndex: 16 }}>
        <Header />
      </div>

      <div style={{ padding: "155px 30px 30px 30px", height: "100%" }}>
        <Space direction="vertical" style={{ width: "100%" }}>
          <Typography.Text>購物車</Typography.Text>
          <List
            locale={{
              emptyText: <Empty description={"你還沒有任何產品"} />,
            }}
            className="demo-loadmore-list"
            itemLayout="horizontal"
            dataSource={cart.current}
            renderItem={(item, index) => {
              const data = MockData.all.find((x) => x.id === item.id);

              return (
                <List.Item
                  actions={[
                    <a
                      key="list-loadmore-more"
                      onClick={() => {
                        cart.remove({ product: data, quality: item.quality });
                      }}
                    >
                      刪除
                    </a>,
                  ]}
                >
                  <Skeleton avatar title={false} loading={item.loading} active>
                    <List.Item.Meta
                      avatar={
                        <Image
                          height={80}
                          style={{ maxWidth: 100 }}
                          src={data.image}
                        />
                      }
                      title={<a>{data.name["zh-TW"]}</a>}
                      description={`x${item.quality}`}
                    />
                    <div>${data.price * item.quality}</div>
                  </Skeleton>
                </List.Item>
              );
            }}
          />
        </Space>
        {cart.current.length > 0 ? (
          <div
            style={{
              padding: "10px",
            }}
          >
            <Row>
              <Col offset={0} span={24}>
                <Divider orientation="right">
                  <Typography.Text>共 {totalProduct} 件商品</Typography.Text>
                </Divider>

                <ProDescriptions column={1} bordered={true}>
                  <ProDescriptions.Item label="商品金額" valueType="text">
                    ${totalAmount}
                  </ProDescriptions.Item>
                  <ProDescriptions.Item label="活動折抵" valueType="text">
                    $0
                  </ProDescriptions.Item>
                  <ProDescriptions.Item label="運費" valueType="text">
                    未選擇
                  </ProDescriptions.Item>
                  <ProDescriptions.Item label="小計" valueType="text">
                    ${totalAmount}
                  </ProDescriptions.Item>
                </ProDescriptions>
              </Col>
            </Row>
            <Button
              type="primary"
              size={"large"}
              block
              style={{ marginTop: 20 }}
              onClick={() => {
                navigate(`/cart/shipping`);
              }}
            >
              結帳
            </Button>
          </div>
        ) : null}
      </div>
    </>
  );
};

export default App;
